import Vue from 'vue'
import Router from 'vue-router'
import Login from "@/page/login/Login"
import Index from "@/page/index/Index"
import Home from "@/page/index/components/Home"
import User from "@/page/user/Index"
import Test from "@/page/user/Test"
import RoleManager from "@/page/role/RoleManager"
import store from '../store/index';


Vue.use(Router)

const router = new Router({
  routes: [
    {path: '/', redirect: '/login'},
    {
      path: '/login',
      name: 'Login',
      meta:"登陆",
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      component: Index,
      children:[
        {
          path:'home',
          //meta:"首页",
          name:"Home",
          meta:{title:'首页',keepAlive:false},
          component:Home
        },
        {
          path:'mgr',
          name:"User",
          meta:{title:'用户管理',keepAlive:true},
          component:User
        },
        {
          path:'notice',
          name:"Notice",
          meta:{title:'通知管理',keepAlive:false},
         // meta:"通知管理",
          component:Test
        },
        {
          path:'role',
          name:"Role",
          meta:{title:'角色管理',keepAlive:true},
          // meta:"通知管理",
          component:RoleManager
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
 // store.dispatch('addOneTab',{text:(to.meta),name:(to.name),path:(to.path)});

  next();
})

export default router



